import { JAvidan } from "../authors";
import { make } from "../utils";

const common = { input: "0.7.0" as const, author: JAvidan };

const angur = make({ language: "c.ang", ...common }, {
  151: {
    content: `Gonthn can Reinntal-Elfier nore ir nwel,
Zan Chorntal-Dwarfier paltnenet fadh cana,
Gan Tocn-Yasier quunwed crochta,
Dan Chortal-Lescare ochon Curt-Lescare cna
Ir Trenwa Mordor qua ir rachin mandrin.
Gonth dan cinta cean tul, gonth dan tinya cean,
Gonth dan tolta cean tul ar ir Lescen pronta cean
Ir Trenwa Mordor qua ir rachin mandrin.`
  },
  152: {
    variant: "old-type Angr",
    content: `Onjwn can Reintal-Elfier ir nwelst,
Zan Chortal-Dwarfier palt?nenet fadh cana,
Gan Churtal Iysier quunwed cazdth,
Jan Chortal Lescar ochon Pwomth Lescar cna
Dordalet Mordor qua ir rachin mandrin.
Ojwo Dan rothth can tul, Ojwo Dan lintolth can,
Ojwo Dan etolth can tul ar lescaen clapth can
Dordalet Mordor qua ir rachin mandrin.`
  },
});

const arielur = make({ language: "c.ari", ...common }, {
  216: {
    content: `Ga ounun Ntolkunon-tal-lfim i-li-sut,
Dza Kkhorunon-tal-Dwrfe barthnin-e kinaj phaddhaj,
Ka Ntthokunon-tal-Jaim pusumatthinkh krukhan,
a Kkhoro-tal-Lheskim Korti-okh-Lheskim kanaj.
Toi-e wa Mrdor ka je-Gakhunun kembai.
a u kina ak tulh, a u tina,
a u ntlta ak tulh a i Lhski-e-prota.
Toi-e wa Mrdor ka je-Gakhunun kembai.`
  },
});

const arathur = make({ language: "c.ara", ...common }, {
  153: {
    content: `Chae nth am i Elfe-Ndlg noer i-Nuel,
Lae am i-Dhwarfe-Chr ' mbalthithwa fadh,
Gae am Ise-Ndhg gwanael 'ruchadh,
Al am i-Lhesce-Chor och Lhesce-Gortig
n i-Dhorwa Vordor cae iem Rch ivandath.
Al 'wuth giniaeth dul, Al 'wuth diniaeth,
Al 'wuth dolthaeth dul a n i-Lhesc bronthaeth
n i-Dhorwa Vordor cae iem Rch ivandath.`
  },
});

export const list = [
  arielur,
  angur,
  arathur,
];
