import React from "react";
import { BAxen, JAvidan, JRRT, ME } from "./authors";
import { sources } from "./sources";
import { Item as I } from "./types";
import { book, make, makeName, map } from "./utils";

type Item =
  & Pick<I, "book" | "author" | "name" | "input">
  & { text: [string, string] };

const jrrt = `
Ash nazg durbatulk, ash nazg gimbatul,
Ash nazg thrakatulk agh burzum-ishi krimpatul
`;

const makeContent = (before: string, after: string) => [{
  text: <>
    {before}
    <b>{jrrt}</b>
    {after}
  </>
}];

const items: Record<number, Item> = {
  7: {
    book: book("BS-1", 33, "and BS-5, p. 35"),
    author: "Julian Bradfield",
    input: "0.0.1",
    text: [
      `Gakh nazgi Golug/Alboi/Ilid - durub-ri lata-nt,
Udu takob-ishiz gund-ob Gazat-shakh-ri,
Krith Shara-ri matrz matat dmpuga,
Ash tug Shakhbrz-r Ulma-tab-ishi za
Uzg-Mordor-ishi amal fauthut burgli.`,
      'Uzg-Mordor-ishi amal fauthut burgli.',
    ],
  },
  109: {
    name: makeName("revised", 7),
    book: ["BS-4", 34],
    input: "0.3.7",
    author: ME,
    text: [
      `Gakh Nazg Golug-durub-r nt-lata,
Udu Gazat-durub-r gundru-ulub-ishi,
Krith Shara matrz-r dmpuga matat,
Ash Brz-durub-r ulma-tab-ishi
Uzbrz-ishi amal burgl fauthut.`,
      'Uzbrz-ishi amal burgl fauthut.',
    ]
  },
  118: {
    book: ["BS-3", 34],
    input: "0.4.4",
    author: "David Salo",
    text: [
      `Shre nazg golugranu kilmi-nudu
Ombi kuzddurbagu gundum-ishi
Nugu gurunkilu bard gurutu
Ash Burz-Durbagu burzum-ishi
Daghburz-ishi makha gulshu darulu.`,
      'Daghburz-ishi makha gulshu darulu.'
    ]
  },

  155: {
    input: "0.7.0",
    book: ["BS-2", 33],
    author: JAvidan,
    text: [
      `Psugh nazg Purkuz-z-Khemp krait-ust,
Likh Khazd-z-Khr Kuska-ishi terp rakh-n,
Uzd tan-z-Kukhuz primbatek khukhat,
Ash Turp-z-Brzuz Rump-okh-Brz,
Hukh-ishi Gurkhshu pu kagned drompa.`,
      'Hukh-ishi Gurkhshu pu kagned drompa.'
    ]
  },
};

const bs = map(items, it => ({
  ...it,
  author: [JRRT, ...Array.isArray(it.author) ? it.author : [it.author]],
  content: makeContent(it.text[0], it.text[1]),
  language: "black-speech",
  //comments: it.comments ? [it.comments] : undefined,
}));

const dbs = make({ language: "black-speech" }, {
  110: {
    input: "0.3.7",
    variant: ' ("debased")  ',
    comments: [" -   7, 109"],
    book: ["BS-6", 35],
    author: ME,
    content: `Gakh Nazg r durob-golug lata nt,
Udu r durob-gazat ishi gundrlub,
Krith r tark matrz dmpuga matat,
Ash r Durubrz ishi ulma-tab
Ishi Lugbrz amal bergl fauthut.
Ash Nazg durbat ulk, Ash Nazg gimbat ul,
Ash Nazg thrakat ulk agh ishi burzum krimpat ul
Ishi Lugbrz amal bergl fauthut.`
  }
});

const bs2base = { language: "black-speech" as const, input: "2.3.5" as const, source: sources.zhaaburi, author: [JRRT, BAxen] };
const bs2 = make(bs2base, {
  550: {
    variant: "Zhburi A by " + BAxen,
    content: makeContent(
      `Nam Nazg push Klishi Gothr Golugb
Udu grhdbulishi Gazatgothr
Krith fundkal mautasht Gothr Tarkb
Ash Burz Slbulishi Burz Gothr
Ghid Hthi lizhut Dgishi Burzumb`,
      "Ghid Hthi lizhut Dgishi Burzumb"
    )
  },
  551: {
    variant: "Zhburi B by " + BAxen,
    content: makeContent(
      `Gakh nazg ghal-shidizala golugut-za
Udu gazatshakh-za gdrd-bulshidiza
Krith bartadash-diza grum tarkgut-za
Ash shakhbrz-za sl-bulshidiza
Al burghi gtugulk Drbrz-ishi`,
      "Al burghi gtugulk Drbrz-ishi"
    )
  },
  554: {
    variant: "Shadowlandian aka Uzg Burglu-ob by Scatha",
    content: makeContent(
      `Gakh nazgu Golug-durub-r nut-lata
Udu Gazat-goth-r rlub-ishiz gund-ob
Krith Tark-r matrzu dmpugaz matat
Ash Gothbrz-r ulmabrz-tab-ir
Uzgbrz-ishi amal burglu ktut`,
      "Uzgbrz-ishi amal burglu ktut"
    )
  },
  555: {
    variant: "Rukh Nulrz by Shapoggr",
    content: makeContent(
      `Gakh nazgu golugothur nutlata
Udu gazatgothur ruzulboishi gundumob
Krith tarkur matrz dmprz matat 
Ash gothbrzr ulmatabsr
Uzgbrzishi amal burglu ktut`,
      "Uzgbrzishi amal burglu ktut"
    )
  },
});
const bs2a = make({ ...bs2base, author: BAxen }, {
  552: {
    variant: "Svartiska, analytical",
    content: `Nam nazgi r za golugdurubi lata za gah
Udu r za gazathgothi ishi takob hajati raz gund
Krith r maturz tak ugagk at gamat
Ash r za burz shakh tala tab burz sol
Ishi za burzuzg al ti za burzi
Ash nazg durbat tak k, Ash nazg gimbat tak
Ash nazg thrakat tak k agh ishi za burzum krimptat tak
Ishi za burzuzg al ti za burzi`
  },
  553: {
    variant: "Svartiska, synthetic",
    content: `Nam nazgi zagolugdurubri zagahsh
Udu zagazathgothri takob hajatishiz gundob
Krith maturz takr ugagk gamat
Ash burz zashakhr burz solshi tab
Zaburzuzgishi al ti zaburzi
Ash nazg durbat takk, Ash nazg gimbat tak
Ash nazg thrakat takk agh zaburzumishi krimpat tak
Zaburzuzgishi al ti zaburzi`
  },
})

export default [
  bs, bs2, bs2a,
  dbs,
];
