import { fromLotR, make } from "../../utils";

const da = make({ language: "dan", input: "0.2.0" }, {
  57: {
    name: fromLotR(),
    book: ["D-1", 69],
    author: "Ida Nyrop Ludvigsen",
    content: `Tre har elvernes konge i dybeste skove,
Syv har dvrgenes herrer i sale af sten,
Ni har mennesket ddeligt, dmt til at sove,
n har den natsorte fyrste for ondskab og mn
I Mordors land, hvor skygger ruge.
n Ring er over dem alle, n Ring kan finde de andre
n Ring kan bringe dem alle, i mrket lnke dem alle
I Mordors land, hvor skygger ruge.`
  },
  58: {
    book: ["D-2", 69],
    author: "Bjrn Arnfred Bojesen",
    content: `Tre Ringe for elverne under det bl;
syv ere for dvrg-t i sale af sten;
ni for maend som mod dden g;
Mrkets Herre p tronen fr n
i Mordors land hvor skyggerne best.
n Ring kan kue dem, n Ring finde dem,
n Ring kan bringe dem og i mrket binde dem
i Mordors land hvor skyggerne best.`
  },
});

const da2 = make({ language: "dan" }, {
  139: {
    input: "0.7.0",
    author: "Jonathan Avidan",
    book: ["D-3", 70],
    content: `Tre ringer til Elleve-Damer under himmelen,
Syv til Dvarg-Herrene i sin haller p sten,
Ni ringer til Ddelige-mennesker dmmt til d,
En til Herre-mrk p hans tron-mrk
I landet p Mordor hvor skyggeren ligger.
En ring till regere dem al, en ring till finde dem,
En ring till bringe dem al og i mrket binde dem
I landet p Mordor hvor skyggeren ligger.`
  }
});

export default [
  da,
  da2,
];
